<template>
    <baidu-map
        class="bm-view"
        ak="YHMXpNzQSpxfmPCQxAjFcsLhHzGpGSKr"
        :scroll-wheel-zoom="true"
        @ready="handler"
        :mapClick="false"
        :center="center"
        :zoom="zoom"
        @click="mapClick"
    >
        <bm-marker :position="bmMark" />
    </baidu-map>
</template>

<script>
import BaiduMap from "vue-baidu-map/components/map/Map.vue";
import BmMarker from "vue-baidu-map/components/overlays/Marker.vue";
export default {
    name: "Markmap",
    components: {
        BaiduMap,
        BmMarker,
    },
    props: {
        markposition: {
            type: Object,
        },
        centerstr: {
            type: String,
        },
        addrinput: {
            type: Object,
        },
    }, //接受父组件传值
    data() {
        return {
            BMap: null,
            center: {},
            zoom: 15,
            bmMark: {},
            localSearch: null,
            map: null,
            addr: "",
        };
    },
    watch: {
        markposition(newVal) {
            // this.handler({ BMap });
            this.setMarker();
        },
        centerstr(newVal) {
            // this.handler({ BMap });
            this.setCenter();
        },
        addrinput: {
            immediate: true,
            deep: true,
            handler: function (val, oldVal) {
                if (!val.addr) {
                    return;
                }
                // this.addrinput = "";

                this.addr = val.addr;
                this.addrToMarker();
            },
        },
    },
    methods: {
        handler({ BMap, map }) {
            let _this = this;
            this.BMap = BMap;
            this.map = map;
            this.setMarker();
            var options = {
                onSearchComplete: function (searchResult) {
                    if (!searchResult || searchResult.getNumPois() <= 0) {
                        return;
                    }
                    var poi = searchResult.getPoi(0);

                    _this.changeLoc(poi.point.lng, poi.point.lat);
                    _this.$emit("callLnglat", poi); //将值传给父组件
                },
            };

            this.localSearch = new this.BMap.LocalSearch(map, options);
            // if (
            //     this.markposition &&
            //     this.markposition.lng &&
            //     this.markposition.lat
            // ) {
            //     this.center.lng = this.markposition.lng;
            //     this.center.lat = this.markposition.lat;
            //     this.zoom = this.options
            //         ? this.options.zoom
            //             ? this.options.zoom
            //             : 15
            //         : 15;
            //     this.bmMark.lng = this.markposition.lng;
            //     this.bmMark.lat = this.markposition.lat;
            // }
            // else {
            //     let geolocation = new BMap.Geolocation();
            //     geolocation.getCurrentPosition(
            //         (res) => {
            //             that.center = res.point;
            //             that.bmMark = res.point;
            //             this.$emit("readyMap", res);
            //         },
            //         { enableHighAccuracy: true }
            //     );
            // }
        },
        setMarker() {
            this.addr = "";
            if (
                this.markposition &&
                this.markposition.lng &&
                this.markposition.lat
            ) {
                this.center = this.markposition;
                this.zoom = 15;
                this.bmMark = this.markposition;
            }
        },
        setCenter() {
            this.addr = "";
            let geoCoder = new BMap.Geocoder();
            // 获取位置对应的坐标
            geoCoder.getPoint(this.centerstr, (point) => {
                this.center = {
                    lng: point.lng,
                    lat: point.lat,
                };
                this.zoom = 15;
            });
        },
        mapClick(e) {
            this.addr = "";
            if (this.BMap) {
                let geoCoder = new this.BMap.Geocoder();
                geoCoder.getLocation(e.point, (res) => {
                    this.bmMark = {
                        lng: res.point.lng,
                        lat: res.point.lat,
                    };
                    this.$emit("callMap", res); //将值传给父组件
                });
            } else {
                this.$emit("callMap", {}); //将值传给父组件
            }
        },
        changeLoc(lng, lat) {
            // this.center.lng = lng;
            // this.center.lat = lat;
            // this.bmMark.lng = lng;
            // this.bmMark.lat = lat;
            this.$set(this.center, "lng", lng);
            this.$set(this.center, "lat", lat);
            this.$set(this.bmMark, "lng", lng);
            this.$set(this.bmMark, "lat", lat);
        },
        addrToMarker() {
            if (!this.addr) {
                return;
            }
            this.localSearch.search(this.addr);
        },
    },
};
</script>


