<template>
    <el-card class="box-card">
        <div slot="header" class="clearfix">
            <span>监测对象分组</span>
        </div>
        <div
            v-for="item in grouplist"
            :key="item.GROUPID"
            :class="item.GROUPID == selGroupId ? 'active' : ''"
            class="group-item"
            @click="groupClick(item)"
        >
            <div>
                <i class="el-icon-s-grid"></i>
                {{ item.groupshortname }}&nbsp;({{ item.count }})
            </div>
            <i
                class="el-icon-close"
                v-if="item.GROUPID == selGroupId"
                style="float: right"
                @click.stop="removeSelected"
            ></i>
        </div>
    </el-card>
</template>

<script>
export default {
    name: "leftgroup",
    props: {
        grouplist: {
            type: Array,
            required: true,
        },
    },
    data() {
        return {
            selGroupId: "",
        };
    },
    methods: {
        groupClick(item) {
            this.selGroupId = item.GROUPID;
            this.$emit("callGroup", item); //将值传给父组件
        },
        removeSelected() {
            this.selGroupId = 0;
            this.$emit("callGroup", {});
        },
    },
};
</script>
<style scoped>
.clearfix:before,
.clearfix:after {
    display: table;
    content: "";
}
.clearfix:after {
    clear: both;
}

.box-card .item.active {
    background: #ebf5ff;
}

.box-card .item:hover {
    background: #ebf5ff;
}
</style>


