
export default {
    data() {
        return {
            groups: [],
            groupSites: []
        }
    },
    methods: {
        getGroups() {
            if (this.$store.state.options.Groups && this.$store.state.options.Groups.length > 0) {
                this.groups = this.$store.state.options.Groups;
            } else {
                this.$store
                    .dispatch("getOptions", ["Groups"])
                    .then((res) => {
                        this.groups = this.$store.state.options.Groups;
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            }
        },
        getGroupSites(groupid, haschildsite) {
            if (this.$store.state.options.Sites && this.$store.state.options.Sites.length > 0) {
                this.groupSites = this.$store.state.options.Sites.filter(
                    (a) => {
                        if (haschildsite) {//包括子监测点
                            return a.GROUPID == groupid;
                        } else {//父监测点
                            return a.GROUPID == groupid && a.SITEID == a.PARENTSITEID;
                        }
                    }
                );
            } else {
                this.$store
                    .dispatch("getOptions", ["Sites"])
                    .then((res) => {
                        this.groupSites = res.filter(
                            (a) => {
                                if (haschildsite) {//包括子监测点
                                    return a.GROUPID == groupid;
                                } else {//父监测点
                                    return a.GROUPID == groupid && a.SITEID == a.PARENTSITEID;
                                }
                            }
                        );
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            }
        },
        clearGroupSites() {
            this.$store.state.options.Sites = [];
            this.$store.state.options.Groups = [];
            this.getGroups();
        }
    },
    created() {
        this.getGroups()
    }
}


